.App {
  display: flex;
  text-align: center;
  justify-content: center;

  font-family: "Avenir Light", sans-serif;
}

p {
  font-size: 16px;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.About {
  font-size: 24px;
  width: 45%;
}

.Title {
  font-weight: bold;
  font-size: 20px;
}

.container {
  display: grid;
  grid-template-columns: 10rem 10rem;
  grid-gap: 1rem;

  column-gap: 500px;
  row-gap: 30px;

  position: absolute;
  top: 45%;
  left: 15%;
}

.item {
  display: block;
  height: 600px;
  width: 600px;
  color: black;
}

img {
  border: 2px solid gray;
}

.topnav {
  overflow: hidden;
  font-family: "Avenir Light", sans-serif;
}

/* Style the links inside the navigation bar */
.topnav a {
  float: left;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: orange;
}

/* Add a color to the active/current link */
.topnav a.active {
  font-size: 30px;
  color: orange;
}

/* Right-aligned section inside the top navigation */
.topnav-right {
  float: right;
}

.About a:link {
  text-decoration: none;
}
.About a:visited {
  color: black;
  text-decoration: none;
}
.About a:hover {
  text-decoration: none;
}
.About a:active {
  text-decoration: none;
}

.hover-underline {
  display: inline-block;
  position: relative;
  color: #0087ca;
}

.hover-underline::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #0087ca;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.hover-underline:hover::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

a,
a:visited,
a:hover,
a:active {
  color: inherit;
  text-decoration: none;
}